	module.exports = {"data":{"markdownRemark":{"html":"<p>文化的覇権と文化主権に関する記事を書いて、ソブリン開発の新しい方向性を分析する。基本的な点は次のとおりです。</p>\n<p>文化的要因は国際関係における伝統的な変数であるが、時代によって重要性が異なる。一般的な論理は、文化的、経済的、軍事的要因が支配的であり、文化的要因の役割が小さいことであり、逆もまた同様である。冷戦の終結は、国際関係における政治的および軍事的要因の役割が相対的に低下し、政治的および軍事的競争が緩やかになり、文化的要因の地位が上昇する機会を作り出した状況を正確に作り出した。過去の文化的要素が存在しない、あるいは明白ではないが、政治や軍などの「強硬な力」が国際関係における文化的要因の可能性を抑え、文化は「ソフトパワー」であるということではない。人間社会の文化的矛盾は、激しい政治的、軍事的競争の状況下では消滅していないが、二次的地位に下がっている。潜在的かつ明白な文化的矛盾は解決されないが、抑制される。このような政治的、軍事的圧力が弱まると、文化的な葛藤や矛盾が浮かび上がって現れます。これはまた国際関係の客観的な力の国際覇権政治の反動でもある。したがって、冷戦後の時代には、文化的要因がますます重要になってきています。</p>\n<p>国際関係における文化的要因の新たな動向には、次の2つの表現形式があります。まず、「文化主権」という概念が優先されます。冷戦後の文化的要因は国際関係において重要な役割を果たしてきた。第二は「文化覇権」の戦略的形成である。国際文化紛争はますます明白になっています。途上国の一般的な状況から、主体の主な任務は「文化主権」を維持することである。先進国の視点から見ると、主な目的は「文化覇権」に取り組むことです。 「文化主権」と「文化覇権」はまだ現象論的な問題であり、深い問題は国家主権の概念への挑戦、あるいは国家の本質への挑戦である。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0051.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0051.md","frontmatter":{"title":"3月6日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0051.md absPath of file >>> MarkdownRemark"}}

